      // this.scrollTop = this.$refs.contentContainer.scrollTop
      // console.log('scrollTop:', this.$refs.contentContainer.scrollTop);
      // 判断是否吸顶效果
      if (this.$refs.contentContainer.scrollTop >= localData.navBarOffsetTop) {
        this.showNavBar = true
      } else {
        this.showNavBar = false
      }
