      //         this.$util.openNewWindow(decodeURIComponent(param.replace('param=', '')), true);
      //       }
      //     }
      //     break
      //   case '7': // 新品
      //     // this.$toast('商品详情页');
      //     if (param) {
      //       if (this.isMiniProgram) {
      //         wx.navigateTo({
      //           url: `/pages/product/detail/index?productId=${param}`,
      //         })
      //       }
      //       if (this.isFromRN) {
      //         window.postMessage(
      //           JSON.stringify({
      //             type: 'goProduct',
      //             data: {
      //               id: param,
      //             },
      //           })
      //         )
      //       } else {
      //         this.$router.push(`/goodsDetail?productId=${param}`)
      //       }
      //     }
      //     break
      // }
    },
    /**
     * 跳转商品
     */
    toProdDetail (prod, id) {
      const { productId } = prod
      if (this.isMiniProgram) {
        wx.miniProgram.navigateTo({
          url: `/pages/product/detail/index?productId=${productId}`,
        })
      } else if (this.isByteDanceMP) {
        tt.miniProgram.navigateTo({
          url: `/pages/product/detail/index?productId=${productId}`,
        })
      } else if (this.isFromRN) {
        window.postMessage(
          JSON.stringify({
            type: 'goProduct',
            data: {
              id: productId,
            },
          })
        )
      } else {
        localData.scrollTopMap.set(this.$route.params.id, this.$refs.contentContainer.scrollTop);
        this.$router.push(`/goodsDetail?productId=${productId}`)
      }
    },
    /**
     * 切换navigationBar
     */
    changeNavigationbar (modualId, b) {
      this.activeNavId = modualId
      setTimeout(() => {
        this.$refs.contentContainer.scrollTo(0, this.$refs[`anchor${modualId}`][0].offsetTop - localData.navBarHeight - this.headerHeight)
      }, 100)
    },
    goBack () {
      // this.$router.back();
      // this.$store.dispatch('doHistoryBack');
      if (this.isFromRN && localData.scrollTopMap.size <= 1) {
        window.postMessage(
          JSON.stringify({
            type: 'goBack',
          })
        )
      } else {
        localData.scrollTopMap.delete(this.$route.params.id);
        this.$store.dispatch('doHistoryBack')
        this.modualList = [];
        this.couponMap = [];
        this.skuMap = [];
        this.title = '';
      }
    },
    toShare () {
      const title = this.title; // 分享标题
      const content = localData.sharedesp; // 分享内容
      const pic = localData.shareimg; // 分享图片，写绝对路径
      let url = config.HOST + '/sgmobile/bannerCustom?id=' + this.$route.params.id + '&shareId=' + this.mId;

      if (this.isFromRN) {
        window.postMessage('custPageShare|' + title + '|' + content + '|' + pic + '|' + url)
      } else if (this.$util.isHaierApp() || this.$util.isIhaierApp()) {
        this.$util
          .inAppShare({
            title,
            desc: content,
            url, // 不传默认当前url
            thumImage: pic,
          })
          .then(res => console.log(res))
      }
    },
  },
  filters: {
    /**
     * 截取数字整数部分  200.12 -> 200
     */
    splitInt (value) {
      if (value) {
        return value.toFixed(2).split('.')[0]
      } else {
        return 0
      }
    },
    /**
     * 截取数字小数部分 200.12 -> .12
     */
    splitFloat (value) {
      if (value) {
        return `.${value.toFixed(2).split('.')[1]}`
      } else {
        return '.00'
      }
    },
  },
}
</script>
